.wrapper{
    /* border:2px solid rgb(161, 161, 161); */
    border-radius: 10px;
    display:grid;
    grid-template-columns: repeat(10, 1fr);

    margin-top:1rem;
    font-size:0.7em;
    min-height:423.27px;
} .formImage{
    background-image:url('../images/stormtrooperonkeyboard.jpg');
    background-size:cover;
    background-position:center;
    border-radius:5px 0px 0px 5px;
    grid-column: 1/4;
}
.formContainer{
    grid-column:4/11;
    padding:0.2em 4em;
}
.add-form{
    margin-top:0;
    min-height:267.44px;
    font-size:1.3em;
}
.label-for-category{
    color: white;
}
.wrapper-era{
    margin-left:.5em;
 }
.label-era{
    color:white;
   
}
.formContainer > h2 {
    color:white;
    font-size:1.5em;
    font-weight:200;
}
.add-form > label{  
    color:white;
    margin-top:1em;
    margin-bottom:0.3em;
    display:block;
}
.add-form > input{
    padding:0.5em;
    border:none;
    border-radius:5px;
    opacity:.5;
    min-width:17em;
}
.add-form > input:hover, input:focus{
    opacity:.9;
}
.invalid{
    outline: 2px solid red;
}
.invalid-message{
    display:none;
}
/* + betyder sibling, direkt efterföljande syskon, skillnad på detta och >  */
.invalid + .invalid-message{
    color:red;
    display:inline-block;
    margin-left:1em;
    font-size:1em;
}
.submit-button{
    margin-left:.2em;
    margin-top:2em;
    margin-bottom:1em;
    padding:0.5em;
    width:10em;
    border:1px solid white;
    background-color:black;
    color:white;
    border-radius:5px;
    display:block;
}
.submit-button:disabled{
    background-color:rgb(148, 121, 121);
}
.submit-button:hover{
    cursor:pointer;
}
.not-submitted{
    color:rgba(255, 255, 255, 0);
    text-align:center;
    margin-top:0;
    padding:.3em;
}
.submitted{
    color:goldenrod;
    font-weight:800;
    padding:.3em;
    margin-top:0;
}




